@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

body, html { height: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) { * { -webkit-text-size-adjust: none; } }
table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

@font-face { font-family: "svgfont"; src: url("fonts/svgfont.eot"); src: url("fonts/svgfont.eot?#iefix") format("eot"), url("fonts/svgfont.woff") format("woff"), url("fonts/svgfont.woff2") format("woff2"), url("fonts/svgfont.ttf") format("truetype"); font-weight: normal; font-style: normal; }
.icon, [class^="icon-"], [class*=" icon-"] { font-family: "svgfont"; font-style: normal; font-weight: normal; text-rendering: auto; speak: none; line-height: 1; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

.icon, [class^="icon-"], [class*=" icon-"] { display: inline-block; }

.icon-sepa:before { content: ""; }

.icon-skrill:before { content: ""; }

@font-face { font-family: "HelveticaNeue Regular"; src: url("fonts/helveticaneuecyr-roman.eot"); src: url("fonts/helveticaneuecyr-roman.eot?#iefix") format("embedded-opentype"), url("fonts/helveticaneuecyr-roman.woff2") format("woff2"), url("fonts/helveticaneuecyr-roman.woff") format("woff"), url("fonts/helveticaneuecyr-roman.ttf") format("truetype"); font-weight: normal; font-style: normal; }
@font-face { font-family: "HelveticaNeue Medium"; src: url("fonts/helveticaneuecyr-medium.eot"); src: url("fonts/helveticaneuecyr-medium.eot?#iefix") format("embedded-opentype"), url("fonts/helveticaneuecyr-medium.woff2") format("woff2"), url("fonts/helveticaneuecyr-medium.woff") format("woff"), url("fonts/helveticaneuecyr-medium.ttf") format("truetype"); font-weight: normal; font-style: normal; }
@font-face { font-family: "HelveticaNeue Bold"; src: url("fonts/helveticaneuecyr-bold.eot"); src: url("fonts/helveticaneuecyr-bold.eot?#iefix") format("embedded-opentype"), url("fonts/helveticaneuecyr-bold.woff2") format("woff2"), url("fonts/helveticaneuecyr-bold.woff") format("woff"), url("fonts/helveticaneuecyr-bold.ttf") format("truetype"); font-weight: normal; font-style: normal; }
body { font-family: "HelveticaNeue Regular", Arial, sans-serif; font-size: 12px; line-height: 1.4; }
body.is-active { overflow-y: scroll; position: fixed; width: 100%; }

h1, h2, h3, h4, h5, h6, strong, b { font-weight: normal; }

a { text-decoration: none; }

.main { min-width: 320px; max-width: 800px; height: 100%; margin: 0 auto; position: relative; }
.main_index { background: url(../img/background.jpg); background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; text-align: center; }
.main_index .container { padding: 37px 0 0; background: url(../img/bg-main.png); background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; background-position: 33% 100%; }

.container { min-height: 100%; margin-bottom: -82px; position: relative; background: #f7eac8; }
@media only screen and (max-width: 600px) { .container { margin-bottom: -41px; } }
.container:after { content: ""; display: block; height: 82px; }
@media only screen and (max-width: 600px) { .container:after { height: 41px; } }

.big-logo { display: inline-block; vertical-align: top; font-size: 0; max-width: 85%; }
.big-logo img { max-width: 100%; }

.btn-play { display: block; margin: 0 auto; font-size: 0; position: absolute; bottom: 82px; left: 50%; margin-left: -158px; width: 317px; box-shadow: 3.5px 6.1px 16px rgba(13, 13, 11, 0.59); border-radius: 60px; }
@media only screen and (max-width: 600px) { .btn-play { width: 160px; margin-left: -80px; bottom: 50px; box-shadow: 1.7px 3px 8px rgba(13, 13, 11, 0.59); border-radius: 30px; } }
.btn-play img { width: 100%; }

.header { background: #79251f; }
.header__main { padding: 54px 38px 52px 17px; *zoom: 1; }
.header__main:after { content: " "; display: table; clear: both; }
@media only screen and (max-width: 600px) { .header__main { padding: 27px 19px 26px 10px; } }

.btn-menu { float: right; width: 52px; border-top: 8px solid #fff; border-bottom: 8px solid #fff; padding: 5px 0; background: none; cursor: pointer; position: relative; z-index: 50; margin-top: 6px; }
@media only screen and (max-width: 600px) { .btn-menu { border-width: 4px; padding: 3px 0; width: 26px; margin-top: 2px; } }
.btn-menu span { height: 8px; display: block; background: #fff; }
@media only screen and (max-width: 600px) { .btn-menu span { height: 4px; } }

.logo { float: left; max-width: 82%; }
.logo img { max-width: 100%; }

.footer { text-align: center; height: 82px; background: rgba(1, 1, 1, 0.7); position: relative; z-index: 10; }
@media only screen and (max-width: 600px) { .footer { height: 41px; } }
.footer_index { background: none; }
.footer__list { font-size: 0; padding: 33px 0; }
@media only screen and (max-width: 600px) { .footer__list { padding: 16px 0; } }
.footer__list li { display: inline-block; padding: 0 7px; }
@media only screen and (max-width: 600px) { .footer__list li { padding: 0 4px; } }
.footer__list a { color: white; font-size: 16px; line-height: 1; }
@media only screen and (max-width: 600px) { .footer__list a { font-size: 9px; } }
.footer__list a:hover { text-decoration: underline; }

.category { clear: both; padding: 6px 0 0; }
@media only screen and (max-width: 600px) { .category { padding: 3px 0 0; } }
.category__title { font-family: "HelveticaNeue Bold", Arial, sans-serif; font-size: 29px; color: #fff; text-transform: uppercase; text-align: center; line-height: 1; margin-bottom: 35px; }
@media only screen and (max-width: 600px) { .category__title { font-size: 14px; margin-bottom: 18px; } }

.tabs { font-size: 0; text-align: center; }
.tabs li { display: inline-block; vertical-align: top; padding: 0 2px; }
@media only screen and (max-width: 600px) { .tabs li { padding: 0 1px; } }
.tabs li a { font-size: 25px; color: white; line-height: 1; padding: 11px 17px 12px; display: inline-block; border: 2px solid rgba(247, 234, 200, 0.2); border-bottom: none; border-radius: 2px 2px 0 0; }
@media only screen and (max-width: 600px) { .tabs li a { font-size: 13px; padding: 6px 9px 6px; } }
.tabs li a.is-active { background: #f7eac8; border-color: #f7eac8; color: #792420; }

.section { font-size: 0; }

.items { text-align: center; padding: 30px 0 50px; margin: 0 auto; }
@media only screen and (max-width: 600px) { .items { padding: 15px 0 25px; max-width: 320px; } }

.item { padding: 28px 19px; width: 288px; display: inline-block; vertical-align: top; text-align: center; }
@media only screen and (max-width: 600px) { .item { padding: 14px 10px; width: 50%; } }
.item__img img { width: 100%; border-radius: 50%; }
.item__play { width: 133px; display: block; margin: -46px auto 0; }
@media only screen and (max-width: 600px) { .item__play { width: 66px; margin-top: -23px; } }
.item__play img { width: 100%; display: block; }
.item__title { color: #450600; font-size: 22px; font-family: "HelveticaNeue Medium", Arial, sans-serif; line-height: 1; text-transform: uppercase; padding: 16px 0 0; }
@media only screen and (max-width: 600px) { .item__title { font-size: 11px; padding: 8px 0 0; } }

.menu { position: fixed; top: 0; bottom: 0; right: 0; background-color: #5a2523; width: 406px; z-index: 30; padding: 150px 105px 30px 60px; overflow: auto; transform: translateX(100%); transition: transform 0.3s; }
@media only screen and (max-width: 600px) { .menu { width: 203px; padding: 75px 50px 15px 30px; } }
.menu.is-active { transform: translateX(0); }
.menu__list a { font-size: 30px; text-transform: uppercase; line-height: 80px; color: white; border-bottom: 2px solid rgba(231, 231, 231, 0.1); display: block; }
@media only screen and (max-width: 600px) { .menu__list a { font-size: 15px; line-height: 40px; border-width: 1px; } }
.menu__list a.is-active, .menu__list a:hover { color: #fbd262; }

.articles { color: #100e10; padding: 35px 50px 60px; }
@media only screen and (max-width: 600px) { .articles { padding: 18px 25px 30px; } }
.articles h3, .articles h4 { line-height: 1; font-family: "HelveticaNeue Medium", Arial, sans-serif; margin-bottom: 25px; }
@media only screen and (max-width: 600px) { .articles h3, .articles h4 { margin-bottom: 12px; } }
.articles h3 { font-size: 28px; text-transform: uppercase; }
@media only screen and (max-width: 600px) { .articles h3 { font-size: 14px; } }
.articles h4 { font-size: 24px; }
@media only screen and (max-width: 600px) { .articles h4 { font-size: 12px; } }
.articles p { font-size: 20px; line-height: 1.6; }
@media only screen and (max-width: 600px) { .articles p { font-size: 10px; } }
.articles p a { color: #792420; text-decoration: underline; }
.articles p a:hover { text-decoration: none; }

.article { padding: 24px 0; }
@media only screen and (max-width: 600px) { .article { padding: 12px 0; } }


